* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ol, ul, li {
  list-style: none;
}

.sandbox {
  width: 134px;
  height: 134px;
  margin: 18% auto 3%;
}

.select {
  width: 400px;
  height: 50px;
  margin: auto;
  text-align: center;
  line-height: 50px;
}

.select a {
  height: 100%;
  font-size: 20px;
  color: #333;
  display: block;
  border: 1px solid #666;
  background: white url(http://www.fila.co.kr/images/sub/bg_heritage_select.png) no-repeat 100% 50%;
}

.select ul {
  width: 398px;
  height: 350px;
  overflow: auto;
  border: 1px solid #666;
  margin-top: -1px;
}

.select ul li {
  font-size: 20px;
  color: #666;
  cursor: pointer;
}

.bg {
  color: #fff !important;
  background-color: #999;
}

#basic,
#impact,
#special {
  font-size: 25px !important;
  color: #333 !important;
}

.popIn {
  animation: popIn 1s forwards;
  animation-delay: !important;
  opacity: 0;
}

@keyframes popIn {
  0% {
    -webkit-transform: scale3d(0, 0, 0);
    transform: scale3d(0, 0, 0);
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  40% {
    -webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
    animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
    -webkit-transform: scale3d(1.08, 1.08, 1.08);
    transform: scale3d(1.08, 1.08, 1.08);
  }
  60% {
    -webkit-animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
  80% {
    -webkit-animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    -webkit-transform: scale3d(1.03, 1.03, 1.03);
    transform: scale3d(1.03, 1.03, 1.03);
  }
  100% {
    -webkit-animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
    opacity: 1;
  }
}

.popInTop {
  animation: popInTop 1s forwards;
  animation-delay: !important;
  opacity: 0;
}

@keyframes popInTop {
  0% {
    -webkit-transform: translate3d(0, -100%, 0) scale3d(0.1, 0.1, 0.1);
    transform: translate3d(0, -100%, 0) scale3d(0.1, 0.1, 0.1);
    opacity: 0;
  }
  40% {
    opacity: 1;
    -webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
    animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
    -webkit-transform: translate3d(0, 0, 0) scale3d(1.08, 1.08, 1.08);
    transform: translate3d(0, 0, 0) scale3d(1.08, 1.08, 1.08);
  }
  60% {
    -webkit-animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    -webkit-transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
    transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
  }
  80% {
    -webkit-animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
    -webkit-transform: translate3d(0, 0, 0) scale3d(1.03, 1.03, 1.03);
    transform: translate3d(0, 0, 0) scale3d(1.03, 1.03, 1.03);
  }
  100% {
    -webkit-animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -webkit-transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
    transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
    opacity: 1;
  }
}

.popInBottom {
  animation: popInBottom 1s forwards;
  animation-delay: !important;
