*{
    margin: 0;
    padding: 0;
}
a{
    text-decoration: none;
}
ol,ul,li{
    list-style: none;
}
@mixin WH($width,$height) {
    width: $width;
    height: $height;
}

.sandbox{
    @include WH(134px,134px );
    margin: 18% auto 3%;
}
.select{
    @include WH(400px,50px );
    margin: auto;
    text-align: center; 
    line-height: 50px;          
    a{  
        height: 100%;        
        font-size: 20px;
        color: #333;
        display: block;
        border:1px solid #666;
        background: rgb(255,255,255) url(http://www.fila.co.kr/images/sub/bg_heritage_select.png) no-repeat 100% 50%;        
    }
    ul {
        width: 398px;
        height: 350px;
        overflow: auto;
        border:1px solid #666;
        margin-top: -1px;    
        li{
        font-size: 20px;
        color: #666; 
        cursor: pointer;
        }
    }
}
.bg{
    color: #fff !important;
    background-color: #999;
}
#basic,
#impact,
#special{
  font-size: 25px !important;
  color: #333 !important;
}


@mixin popIn($ani_time, $ani_repetitions, $delay: null) {

    @keyframes popIn {
        0% {
          -webkit-transform: scale3d(0, 0, 0);
          transform: scale3d(0, 0, 0);
          opacity: 0; }
        20% {
          opacity: 1; }
        40% {
          -webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
          animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
          -webkit-transform: scale3d(1.08, 1.08, 1.08);
          transform: scale3d(1.08, 1.08, 1.08); }
        60% {
          -webkit-animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
          animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
          -webkit-transform: scale3d(1, 1, 1);
          transform: scale3d(1, 1, 1); }
        80% {
          -webkit-animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
          animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
          -webkit-transform: scale3d(1.03, 1.03, 1.03);
          transform: scale3d(1.03, 1.03, 1.03); }
        100% {
          -webkit-animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
          animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
          -webkit-transform: scale3d(1, 1, 1);
          transform: scale3d(1, 1, 1);
          opacity: 1; } }
  animation: popIn $ani_time $ani_repetitions;
  animation-delay: $delay !important;
  opacity: 0;
}
.popIn{    
    @include popIn(1s,forwards);
}


